.content{
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.downloadMessage{
    width: 95%;
    display: flex;
    align-items: center;
    background: #f44336;
    height: 36px;
    padding: 0 40px;
    margin: 20px 0 35px 0;
    color:white;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0,0.16),0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
#image-downloader{
    width: 10.5%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 20px 0 35px 0;
}
.downloadText{
    text-align: start;
    flex: 1;
}
@media (max-width:995px){
    .downloadMessage{
        display: none;
    }
} 
.loadMore{
    cursor: pointer;
    background-color:white;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),0 2px 10px 0 rgba(0, 0, 0, 0.12);
    padding: 7px 25px;
    font-size: 13px;
    margin-top: -20px;
}